import { BrowserModule } from '@angular/platform-browser';
import {BrowserAnimationsModule} from '@angular/platform-browser/animations';
import { NgModule } from '@angular/core';

// Router
import {RouterModule, Routes, Router} from '@angular/router';


// Material Components
import {MatToolbarModule} from '@angular/material/toolbar';
import {MatButtonModule} from '@angular/material/button';
import {MatCardModule} from '@angular/material/card';

// Scrolling
import {ScrollToModule} from 'ng2-scroll-to';


// components
import { AppComponent } from './app.component';
import { IntroComponent } from './intro/intro.component';
import { ProjectsComponent } from './projects/projects.component';
import { ContactmeComponent } from './contactme/contactme.component';
import { FeaturedComponent } from './featured/featured.component';
import { AdminComponent } from './admin/admin.component';
import { HomeComponent } from './home/home.component';
import { ExperiencesModule } from './experiences/experiences.module';
import { CommonModule } from '@angular/common';
import { HttpClientModule } from '@angular/common/http';
import PikyAddQuestionsComponent from './piky-add-questions/component/piky-add-questions.component';
import { FormsModule } from '@angular/forms';
import PikyService from './piky-add-questions/piky.service';

export const routes: Routes = [
		{path: '', component: HomeComponent},
    {path: 'admin', component: AdminComponent},
    {path: 'piqy', component: PikyAddQuestionsComponent}  
];

@NgModule({
  declarations: [
    AdminComponent,
    AppComponent,
    ContactmeComponent,
    FeaturedComponent,
    HomeComponent,
    IntroComponent,
    PikyAddQuestionsComponent,
    ProjectsComponent
  ],
  imports: [
    BrowserModule,
    BrowserAnimationsModule,
    CommonModule,
    ExperiencesModule,
    FormsModule,
    HttpClientModule,
    MatToolbarModule,
    MatButtonModule,
    MatCardModule,
    ScrollToModule.forRoot(),
    RouterModule.forRoot(routes)
  ],
  providers: [ PikyService ],
  bootstrap: [ AppComponent ],
  exports: [ RouterModule ]
})
export class AppModule { }
