import { Component } from "@angular/core";
import PikyService from '../piky.service';




@Component({
  selector: 'piky-add-questions.component.html',
  templateUrl: 'piky-add-questions.component.html',
  styleUrls: ['piky-add-questions.component.scss']
})

export default class PikyAddQuestionsComponent {

  questionText: string;
  newOption: string;
  options;
  pass: string;
  questionSendStatus: string;
  quesDate: string;


  constructor(
    private pikyService: PikyService
  ) {
    this.options = [];
    this.questionText = "";
    this.newOption = "";
    this.questionSendStatus = "neutral";
    const today = new Date();
    const dd = String(today.getDate()).padStart(2, '0');
    const mm = String(today.getMonth() + 1).padStart(2, '0');
    const yyyy = today.getFullYear();
    this.quesDate = dd + '-' + mm + '-' + yyyy;
  }

  sendQuestion() {
    let question: any = {};
    question.questionText = this.questionText.replace(/oo/g, '{USER}');
    question.options = this.options.map(o => { return { optionText: o }});
    let s = this.pikyService.sendQuestion({ question, pass: this.pass, dateAdded: this.quesDate })
      .subscribe(resp => {
        this.questionSendStatus = resp.message;
        if (resp.message === "success") {
          (<HTMLInputElement>document.getElementById("passField")).value='';
        }
        s.unsubscribe();
      });
  }

  deleteOptionAt(i) {
    this.options.splice(i, 1);
  }

  addOption() {
    this.options.push(this.newOption);
    (<HTMLInputElement>document.getElementById("newOptionField")).value = '';
    document.getElementById("newOptionField").focus();
    (<HTMLInputElement>document.getElementById("newOptionField")).select();
  }


}
